import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PromosiRoutingModule } from './promosi-routing.module';
import { DetailComponent } from './detail/detail.component';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { ApprovalModule } from 'src/app/components/approval/approval.module';
import { AddComponent } from './add/add.component';
import { DocumentModule } from 'src/app/components/document/document.module';
import { PipeModule } from 'src/app/pipe/pipe.module';


@NgModule({
    declarations: [DetailComponent, ListComponent, EditComponent, AddComponent],
    imports: [
        CommonModule,
        PromosiRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        ApprovalModule,
        DocumentModule,
        PipeModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]
})
export class PromosiModule { }
