import { Pegawai_master } from 'src/app/sdk/hr/models/pegawai-_master';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DirektoriPromosiService, CompanyJabatanService, DirektoriPegawaiMasterService, DirektoriHrNotesService, CompanyDivisiService, CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Promosi, Jabatan, Hr_notes, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {
    constructor(
        private fb: FormBuilderTypeSafe,
        private direktoriPromosiService: DirektoriPromosiService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyDivisiService: CompanyDivisiService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Promosi = {}
    listJabatan: Jabatan[]= []
    listPegawai: Pegawai_master[]= []

    toogleUpload = false
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_pegawai_promosi')

        this.form()
        this.getPreeData()
        this.getData()
        this.getDepartemen()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hrmanagement_promosi.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data
            }
        )
    }

    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []
    getDepartemen() {

        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.ke_id_departemen,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getJabatan() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatans(JSON.stringify(
            {
                id_divisi: this.dataForm.value.ke_id_divisi,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listJabatan = data.data;
                    if(data.data.length < 1)
                    {
                        this.pesan.pesanWarningForm('data jabatan kosong!')
                    }
                }
            )

    }


    listNotes: Hr_notes[] = []
    getNotes() {
        this.direktoriHrNotesService.getDirektoriHrNotes(JSON.stringify({id_pegawai: this.dataForm.value.id_pegawai})).subscribe(
            data => {
                this.listNotes = data.data
            }
        )
    }

    id: string = ''
    getData() {
        this.spinner.show()
        this.direktoriPromosiService.getDirektoriPromosisId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hrmanagement_promotion",
                    for_table: "erp_hr.promosi",
                    for_module_id: data.data.id_pegawai_promosi
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Promosi>
    form() {
        this.dataForm = this.fb.group<Promosi>(
            {
                id_pegawai_promosi: new FormControl(''),
                id_user: new FormControl(''),
                tanggal_promosi: new FormControl(''),
                ke_id_jabatan: new FormControl(''),
                dari_id_pegawai_posisi: new FormControl(''),
                status_promosi: new FormControl(''),
                keterangan: new FormControl(''),
                id_pegawai: new FormControl(''),
                ke_id_atasan: new FormControl(''),
                ke_id_departemen: new FormControl(''),
                ke_id_divisi: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),
                id_data_flow: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                nomor: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_hr_notes: new FormControl(''),
            }
        )
    }
    simpan() {
        this.spinner.show()

        this.data = this.dataForm.value
        this.direktoriPromosiService.putDirektoriPromosisId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/promosi/list'])
    }
}
