import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Jabatan, Promosi, Pegawai, V_pegawai_posisi, Hr_notes, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { Component, OnInit } from '@angular/core';
import { DirektoriPromosiService, CompanyJabatanService, DirektoriPegawaiService, DirektoriPegawaiPosisiService, DirektoriVPegawaiPosisiService, DirektoriHrNotesService, CompanyDivisiService, CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriPromosiService: DirektoriPromosiService,
        private companyJabatanService: CompanyJabatanService,
        private companyDivisiService: CompanyDivisiService,
        private companyDepartemenService: CompanyDepartemenService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService
    ) { }



    promosi: Promosi = {}
    pegawai: Pegawai = {}
    pegawaiAtasan: Pegawai = {}
    pegawai_posisi: V_pegawai_posisi = {}
    jabatan: Jabatan = {}
    departemen: Departemen = {}
    divisi: Divisi = {}

    id_pegawai_promosi: string = ''

    ngOnInit() {
        this.id_pegawai_promosi = this.routerActive.snapshot.paramMap.get('id_pegawai_promosi')
        console.log(this.id_pegawai_promosi)
        this.getPromosi();
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getPromosi();
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getPromosi() {
        this.spinner.show()
        this.direktoriPromosiService.getDirektoriPromosisId(this.id_pegawai_promosi).subscribe(
            response => {
                this.promosi = response.data
                this.getPegawai(response.data.id_pegawai)
                this.getPegawaiAtasan(response.data.ke_id_atasan)
                this.getPegawaiPosisi(response.data.id_pegawai)
                this.getJabatan(response.data.ke_id_jabatan)
                this.getNotes(response.data.id_hr_notes)
                this.getDepartemen(response.data.ke_id_departemen)
                this.getDivisi(response.data.ke_id_divisi)

                this.params = {
                    id: response.data.id_pegawai_promosi,
                    id_data_flow_detail: response.data.id_data_flow_detail,
                    id_data_flow: response.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_pegawai_promosi"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )

    }

    dataNotes: Hr_notes = {}
    getNotes(id_hr_notes) {
        this.direktoriHrNotesService.getDirektoriHrNotesId(id_hr_notes).subscribe(
            response => {
                this.dataNotes = response.data
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data
                this.spinner.hide()
            }
        )
    }

    getPegawaiAtasan(ke_id_atasan: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(ke_id_atasan).subscribe(
            response => {
                this.pegawaiAtasan = response.data
                this.spinner.hide()
            }
        )
    }

    getJabatan(ke_id_jabatan: string) {
        this.companyJabatanService.getCompanyJabatansId(ke_id_jabatan).subscribe(
            response => {
                this.jabatan = response.data
            }
        )
    }

    getDepartemen(ke_id_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(ke_id_departemen).subscribe(
            response => {
                this.departemen = response.data
            }
        )
    }

    getDivisi(ke_id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(ke_id_divisi).subscribe(
            response => {
                this.divisi = response.data
            }
        )
    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            response => {
                this.pegawai_posisi = response.data
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_pegawai_promosi })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/promosi/list'])
    }

}
